.landingpage {
  overflow-y: visible;
}
.landingpage__container {
  width: 100vw;
  display: flex;
  flex-direction: column;
  align-items: center;
  max-width: 31.625rem;
  margin: 0 auto;
  padding: 4rem 0;
}
@media screen and (min-width: 64rem) {
  .landingpage__container {
    height: 100vh;
    padding: 0 3.125rem;
    flex-direction: row-reverse;
    justify-content: center;
    max-width: 75rem;
    margin: 0 auto;
  }
}
.landingpage__gambar {
  display: flex;
  justify-content: center;
  align-self: center;
}
.landingpage__gambar img {
  width: 100%;
  padding: 2.5rem;
}
@media screen and (min-width: 64rem) {
  .landingpage__gambar img {
    padding: 0rem;
  }
}
@media screen and (min-width: 64rem) {
  .landingpage__gambar {
    flex: 1 1 0px;
  }
}
@media screen and (min-width: 64rem) {
  .landingpage__text {
    flex: 1 1 0px;
  }
}
.landingpage__text h1 {
  text-align: center;
  font-weight: 500;
  font-size: 2.5rem;
  font-weight: 600;
}
@media screen and (min-width: 37.5rem) {
  .landingpage__text h1 {
    font-size: 3.125rem;
  }
}
@media screen and (min-width: 64rem) {
  .landingpage__text h1 {
    text-align: left;
    font-size: 4rem;
    flex: 1 1 0px;
  }
}
.landingpage__text p {
  font-size: 0.875rem;
  text-align: center;
  padding: 1.25rem 2.5rem 1.25rem 2.5rem;
  font-weight: 400;
  line-height: 1.75;
}
@media screen and (min-width: 37.5rem) {
  .landingpage__text p {
    font-size: 1.375rem;
  }
}
@media screen and (min-width: 64rem) {
  .landingpage__text p {
    padding-left: 0;
    text-align: justify;
    font-size: 1.25rem;
    padding: 1.25rem 7.5rem 1.25rem 0px;
  }
}
.landingpage__button {
  display: flex;
  gap: 0.75rem;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}
@media screen and (min-width: 64rem) {
  .landingpage__button {
    justify-content: flex-start;
    align-items: flex-start;
  }
}
@media screen and (min-width: 64rem) {
  .landingpage__button .button__putih {
    display: none;
  }
}